// @import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600;700;900&display=swap');

* {
  margin: 0;
  padding: 0;

  --color-tag-1: #904e55;
  --color-tag-2: #e26d5c;
  --color-tag-3: #b68cb8;
  --color-tag-4: #0a81d1;
  --color-tag-5: #537a5a;
  --color-tag-6: #0c8346;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  .MuiInputBase-root,
  .MuiTypography-root {
    color: var(--color-foreground-1);
  }
  .MuiInputLabel-root {
    color: var(--color-foreground-2);
  }
  // overflow-y: overlay;
}

body.oak-dialog-open {
  // height: 100vh;
  overflow-y: hidden;
  padding-right: var(--scrollbar-width);
}

.body {
  font-family: 'Source Sans Pro', sans-serif;
  background-color: var(--color-background-1);
  color: var(--color-foreground-1);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

// 34ba9b
// f68c70

:root {
  --color-pick: #4b5db3;
  // --color-pick: #7A93DC;
  --height-nav: 50px;

  --color-themecolor-1: #69a7bf;
  --color-themecolor-2: #99587b;
  --color-themecolor-3: #a66c26;
  --color-themecolor-4: #37ae82;
  --bs-sm: 0 1px 2px rgba(0, 0, 0, 0.05), 0 1px 4px rgba(0, 0, 0, 0.05),
    0 2px 8px rgba(0, 0, 0, 0.05);
  --bs-md: 0 1px 3px rgba(0, 0, 0, 0.06), 0 2px 6px rgba(0, 0, 0, 0.06),
    0 3px 8px rgba(0, 0, 0, 0.09);
  --bs-lg: 0 1px 4px rgba(0, 0, 0, 0.09), 0 3px 8px rgba(0, 0, 0, 0.09),
    0 4px 13px rgba(0, 0, 0, 0.13);

  --logo-height: 30px;
  @media (max-width: 767px) {
    --logo-height: 25px;
  }

  .themecolor1 {
    --color-dark-primary-1: #01a982; //rgba(1,169,130,.9);
    --color-dark-primary-2: #18B08D;
    --color-dark-secondary-1: #69a7bf;
    --color-dark-tertiary-1: #376b8c;
    --color-dark-tertiary-1-trans: rgba(55, 107, 140, 0.2);
    --color-light-primary-1: #18B08D;
    --color-light-primary-2: #01a982;
    --color-light-secondary-1: #69a7bf;
    --color-light-tertiary-1: #91ccd9;
    --color-light-tertiary-1-trans: rgba(145, 204, 217, 0.2);
  }

  .theme_dark {
    --color-background-transparent-1: rgba(0, 0, 0, 0.1);
    --color-background-transparent-2: rgba(0, 0, 0, 0.2);
    --color-background-transparent-3: rgba(0, 0, 0, 0.3);
    --color-background-transparent-4: rgba(0, 0, 0, 0.4);
    --color-background-transparent-5: rgba(0, 0, 0, 0.5);
    --color-background-transparent-6: rgba(0, 0, 0, 0.6);
    --color-background-transparent-7: rgba(0, 0, 0, 0.7);
    --color-background-transparent-8: rgba(0, 0, 0, 0.8);
    --color-background-transparent-9: rgba(0, 0, 0, 0.9);
    --color-background-1: #121212;
    --color-background-2: #1e1e1e;
    --color-background-3: #272727;
    --color-background-4: #2e2e2e;
    --color-background-5: #353535;
    --color-foreground-1: rgba(255, 255, 255, 0.9);
    --color-foreground-2: rgba(255, 255, 255, 0.8);
    --color-foreground-3: rgba(255, 255, 255, 0.7);
    --color-foreground-4: rgba(255, 255, 255, 0.6);
    --color-foreground-5: rgba(255, 255, 255, 0.5);
    --color-overlay: rgba(140, 140, 140, 0.2);
    --color-primary-1: var(--color-dark-primary-1);
    --color-primary-2: var(--color-dark-primary-2);
    --color-secondary-1: var(--color-dark-secondary-1);
    --color-tertiary-1: var(--color-dark-tertiary-1);
    --color-tertiary-1-trans: var(--color-dark-tertiary-1-trans);
    --color-default: rgba(217, 217, 217, 1);
    --color-selected: rgba(217, 217, 217, 1);
    --color-hover: rgba(217, 217, 217, 0.2);
    --color-success: #437a34;
    --color-warning: #49a078;
    --color-failure: #8c030e;

    --color-sidebar-header: #444444;
    --color-sidebar-header-active: var(--color-tertiary-1-trans);
    --color-sidebar-element: #2d2d2d;
    --color-sidebar-fg-hover: rgba(255, 255, 255, 1);
    --color-sidebar-fg: rgba(255, 255, 255, 0.7);

    --color-nav: #2a3036;
    --color-nav-shadow: rgba(252, 252, 252, 0.15);
    --color-nav-text: var(--color-foreground-1);
    --color-nav-hover: rgba(239, 91, 91, 0.1);
    --color-nav-active: var(--color-primary-1);
    --color-nav-slider: var(--color-background-1);
    --color-navitem-mobile: var(--color-foreground-1);

    --color-btn-bg-primary: var(--color-primary-1);
    --color-btn-bg-primary-hover: var(--color-primary-2);
    --color-btn-fg-primary: var(--color-foreground-1);
    --color-btn-fg-primary-hover: black;
    --color-btn-bg-secondary: var(--color-secondary-1);
    --color-btn-fg-secondary: var(--color-foreground-1);
    --color-btn-fg-secondary-hover: black;
    --color-btn-bg-tertiary: var(--color-tertiary-1);
    --color-btn-fg-tertiary: var(--color-foreground-1);
    --color-btn-fg-tertiary-hover: black;
    --color-btn-bg-default: var(--color-default);
    --color-btn-fg-default: var(--color-foreground-1);
    --color-btn-fg-default-hover: black;

    --color-btn-action-danger-bg: #dc758f;

    --color-dialog: var(--color-nav);

    --color-tag: #404040;
    --color-box-shadow: rgba(255, 255, 255, 0.2);
  }

  .theme_light {
    --color-background-1: #fcfcfc;
    --color-background-2: #e6e6e6;
    --color-foreground-1: #252525;
    --color-foreground-2: #6e6e6e;
    --color-overlay: rgba(22, 22, 22, 0.2);

    --color-primary-1: var(--color-light-primary-1);
    --color-primary-2: var(--color-light-primary-2);
    --color-secondary-1: var(--color-light-secondary-1);
    --color-tertiary-1: var(--color-light-tertiary-1);
    --color-tertiary-1-trans: var(--color-light-tertiary-1-trans);
    --color-default: rgba(64, 64, 64, 1);
    --color-selected: rgba(64, 64, 64, 1);
    --color-hover: rgba(64, 64, 64, 0.2);
    --color-success: #437a34;
    --color-warning: #49a078;
    --color-failure: #8c030e;

    --color-sidebar-header: #e6e6e6;
    --color-sidebar-header-active: var(--color-tertiary-1-trans);
    --color-sidebar-element: var(--color-background-1);
    --color-sidebar-fg-hover: rgba(0, 0, 0, 1);
    --color-sidebar-fg: rgba(0, 0, 0, 0.7);

    --color-nav: #ececec;
    --color-nav-shadow: rgba(0, 0, 0, 0.15);
    --color-nav-text: var(--color-foreground-1);
    --color-nav-hover: rgba(239, 91, 91, 0.1);
    --color-nav-active: var(--color-primary-1);
    --color-nav-slider: var(--color-background-1);
    --color-navitem-mobile: var(--color-foreground-1);

    --color-btn-bg-primary: var(--color-primary-1);
    --color-btn-bg-primary-hover: var(--color-primary-2);
    --color-btn-fg-primary: var(--color-foreground-1);
    --color-btn-fg-primary-hover: white;
    --color-btn-bg-secondary: var(--color-secondary-1);
    --color-btn-fg-secondary: var(--color-foreground-1);
    --color-btn-fg-secondary-hover: white;
    --color-btn-bg-tertiary: var(--color-tertiary-1);
    --color-btn-fg-tertiary: var(--color-foreground-1);
    --color-btn-fg-tertiary-hover: white;
    --color-btn-bg-default: var(--color-default);
    --color-btn-fg-default: var(--color-foreground-1);
    --color-btn-fg-default-hover: white;

    --color-btn-action-danger-bg: #b33951;

    --color-dialog: var(--color-background-1);

    --color-tag: #e6e6e6;
    --color-box-shadow: rgba(0, 0, 0, 0.3);
  }
}

